import { Component } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { CanActivate, Router, RouterStateSnapshot } from '@angular/router';
import{additionalService} from '../models/additionalService';

@Component({
  selector: 'app-additionalServices',
  templateUrl: './additionalServices.html',
  styleUrls: ['./additionalServices.css']
})
export class AdditionalServicesComponent {
  additionalService:additionalService;
  headers = new Headers();
  options:RequestOptions;
  constructor(private http: Http, private router: Router) {
    this.additionalService = new additionalService();
    this.headers.set('Content-Type','application/json');
    this.headers.set('Access-Control-Allow-Origin', '*');
    this.options = new RequestOptions({ headers: this.headers });
    this.http.get('http://localhost:8080/admin/viewAllAdditionalServices')
    .subscribe(
        json => {
          console.log(json);
        });
  }
  save(data) {
    this.http.post('http://localhost:8080/admin/addNewAdditionalService',JSON.stringify(data), this.options)
    .subscribe((json: Object) => {
          console.log(json);
       },
                err => { console.error(err) }
          );
    }
}